import React from "react";
import ReactApexChart from "react-apexcharts";
import { reArrangeSeriesData } from "../../../../../../utils";

class ChartDataBasedOnUrl extends React.Component {
  constructor(props) {
    super(props);

    const e = reArrangeSeriesData(props.data)
  
    this.state = {
      series: [{
        data: e.labels
      }],
      options: {
           colors:  ['#546FED'],
           chart: {
            height: 350,
            type: props.type,
            animations: {
              enabled: true,
              easing: 'bar',
              dynamicAnimation: {
                speed: 1000
              }
            },
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            },
          },
        plotOptions: {
            bar: {
              borderRadius: 8,
              horizontal: true,
              barHeight: 15,  // Controls the height of the bar
            } 
          },
          dataLabels: {
            enabled: false,
          },
          stroke: {
            curve: 'smooth'
          },
       legend: {
            show: true,
    //        position: 'top',
            itemMargin: {
              horizontal: 20,
              vertical: 0
            },
          },
          
         tooltip: {
            enabled: true, 
            custom: function({ series, seriesIndex, dataPointIndex, w }) {
              return `
                <div style="padding: 10px; background-color: #fff; border: 1px solid #ccc; border-radius: 5px;">
                   <span style="color:#9b9ea3; font-size:13px"> ${dataPointIndex,w.config.xaxis.categories[seriesIndex]}</span><br/>
                   <strong>${series[seriesIndex][dataPointIndex]} Hours </strong>
                </div>`;
            }
          },
        annotations: {
          points: [
            {
              x:  e.labels[e.maxIndex], // Dynamically set based on last category
              seriesIndex: e.labels[e.maxIndex],
              marker: {
                size: e.newSeries.length > 0 ? 0 : 0, // Highlight the last marker
                fillColor: "#546FED",
                strokeColor: "#fff",
                strokeWidth: 2,
                shape: "circle",
                cssClass: "screen-marker", // Apply custom CSS class for blinking effect
              },
              label: {
                 borderColor: "#546FED",
              //    offsetY: 0, // Move the label 10px upwards
              // //   position: 'left', // or 'right', 'top', 'bottom'
              //    text: e.newSeries[e.maxIndex] ?  e.newSeries[e.maxIndex] + ' '+  e.series[e.maxIndex]+ ' Hours' : '',
              //    style: {
              //     color: "#546FED",
              //     background: "transparent", // Make label background transparent
              //     zIndex: 0, // Lower z-index to ensure marker is on top
              //     flexWrap: 'wrap',
              //     wordWrap: 'break-word' ,
              //     transform: 'rotate(90deg)', // Rotate the text to create a vertical effect
              //   },
               // orientation: "vertical", // Custom orientation for vertical label
               },
            }],
          xaxis: [{
            x: e.labels[e.maxIndex],
            borderColor: '#000',
            label: {
              borderColor: '#fff',
              style: {
                color: '#000',
                background: '#fff',
              },
            }
          }],
        },
        dataLabels: {
          enabled: false
        },
        xaxis: {
          categories: e.newSeries,
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        yaxis: {
          reversed: false,
          axisTicks: {
            show: true
          },
          labels: {
            rotate: -35, // Rotate the labels by -35 degrees
            formatter: function (value) {
              const maxLength = 12; // Define your max length
              return value.length > maxLength ? value.substring(0, maxLength) + '...' : value;
            }
          }
        }
      },
    };
  }

  render() {
    return (
      <div>
        <div id="chart">
           <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height={350} />
        </div>
      </div>
    );
  }
}

export default ChartDataBasedOnUrl